/*-------------------------------------------
    01. General styles
---------------------------------------------*/

html,
body {
    min-height: 100%;
    background-color: #eee;
    padding: 0;
    font-family: 'Roboto-Regular';
    margin: 0;
}

*::selection {
    color: #6CAFEC;
    background-color: #eee;
}

*::-moz-selection {
    color: #6CAFEC;
    background-color: #eee;
}

*::-webkit-selection {
    color: #6CAFEC;
    background-color: #eee;
}

img {
    width: 100%;
    pointer-events: none;
}

hr {
    padding: 0;
    border: 1.90px solid #eee;
}

code {
    background-color: #eee;
    padding: 4px 12px;
}

button {
    border: 0;
    padding: 0 6px !important;
    border-radius: 3px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .26);
    letter-spacing: .01em;
    margin: 6px 8px 6px 0;
    line-height: 36px;
    min-height: 36px;
    color: rgba(58, 58, 58, 0.87);
    cursor: pointer;
    min-width: 88px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 14px;
    font-style: inherit;
    font-variant: inherit;
    font-family: inherit;
    background-color: rgba(250, 250, 250, 0.89);
}

button.primary,
button.accent,
button.warn {
    color: rgba(250, 250, 250, 0.89);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-bottom: 1rem;
    margin-top: 1rem;
    word-break: break-word;
    line-height: inherit;
    padding: 0.34rem 0;
}

[ng-click] {
    cursor: pointer;
}

p {
    font-size: 14px;
    font-weight: 400;
    letter-spacing: .01em;
    line-height: 20px;
}

input,
textarea {
    padding: 20px 10px 10px 5px;
    display: block;
    width: 98.666%;
    border: none;
    border-bottom: 1px solid #eee;
}

textarea {
    resize: none;
}

input:focus,
textarea:focus {
    outline: none;
}

label {
    color: #999;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    -webkit-transition: all ease 0.2s;
    -moz-transition: all ease 0.2s;
    -o-transition: all ease 0.2s;
    transition: all ease 0.2s;
}

input:focus ~ label,
input.ng-dirty ~ label,
textarea:focus ~ label,
textarea.ng-dirty ~ label {
    top: -18px;
    color: #999;
    font-size: 12px;
}

.left-side {
    width: 340px;
    height: auto;
    display: block;
    margin: 18px 0;
    padding: 0;
    background-color: transparent;
    position: relative;
    z-index: 1;
}

.right-side {
    width: 75%;
    /*height: 100%;*/
    position: absolute;
    right: 24px;
    top: 110px;
    z-index: 4;
}

.loads {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 9999;
    background-color: #aaaaaa;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    -o-transition: all ease 0.3s;
    transition: all ease 0.3s;
}

.loads h1 {
    text-align: center;
    color: #fff;
    letter-spacing: 1.2px;
    font-size: 1.4em;
    top: 38%;
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
}

.loads > .pre-loader {
    top: 100px;
}

.pre-loader {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 111;
    opacity: 1;
}

.pre-loader > img {
    margin-right: auto;
    margin-left: auto;
    right: 0;
    left: 0;
    margin-top: 0;
    display: block;
    width: 60px;
    -webkit-animation: rotating 400ms infinite;
    -o-animation: rotating 400ms infinite;
    animation: rotating 400ms infinite;
}

.info-content {
    width: 100%;
    margin: 12px 0;
    position: relative;
    padding: 1px 0 28px;
    border-bottom: 1px solid rgba(200, 200, 200, 0.3);
    display: inline-block;
}

.info-content > .title {
    font-family: 'Roboto-Bold';
    letter-spacing: 1px;
    color: #666;
}

.close-button {
    width: 45px;
    height: 45px;
    min-width: 45px;
    padding: 0 !important;
    border-radius: 50%;
    display: block;
    margin: -36px auto 0 auto;
    position: relative;
    background-image: url('/img/icons/close.svg');
    background-position: center;
    background-size: 50%;
    background-repeat: no-repeat;
    margin-right: auto;
    cursor: pointer;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .14), 0 2px 2px 0 rgba(0, 0, 0, .098), 0 1px 5px 0 rgba(0, 0, 0, .084);
}

.info-content > .second-title {
    font-family: 'Roboto-LightItalic';
    letter-spacing: 0.78px;
    color: #6e6e6e;
}

.info-content > p {
    font-family: 'Roboto-Regular';
    letter-spacing: 0.65px;
}

