/*-------------------------------------------
    04. All the pages common styles
---------------------------------------------*/

.simple-page {
    width: 100%;
    height: auto;
    margin: 0 0 36px;
    min-height: 120px;
    background-color: #fff;
    position: relative;
    padding: 0 34px 5px;
    color: #666;
    display: inline-block;
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .14), 0 4px 5px 0 rgba(0, 0, 0, .098), 0 1px 10px 0 rgba(0, 0, 0, .084);
    -webkit-transition: all ease 0.6s;
    -moz-transition: all ease 0.6s;
    -o-transition: all ease 0.6s;
    transition: all ease 0.6s;
}

.portfolio-page {
    height: auto;
    margin: 0 0 20px;
    min-height: 120px;
    background-color: #fff;
    position: relative;
    padding: 0 34px;
    color: #666;
    display: inline-block;
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .14), 0 4px 5px 0 rgba(0, 0, 0, .098), 0 1px 10px 0 rgba(0, 0, 0, .084);
}


.info-content:last-child {
    border-bottom: none;
}

/*-------------------------------------------
    05. All common pages titles
---------------------------------------------*/

.price-page > .title,
.activities-page > .title {
    padding: 32px 34px 22px 34px !important;
}

.simple-page > .title,
.portfolio-page > .title {
    font-family: 'Roboto-Bold';
    padding: 22px 0;
    font-size: 32px;
    word-wrap: break-word;
    font-weight: 400;
    text-transform: uppercase;
}

.single-portfolio-page > .title {
    font-family: 'Roboto-Bold';
    background-color: transparent !important;
    padding: 32px 0 22px;
    font-size: 32px;
    letter-spacing: 1.2px;
    word-wrap: break-word;
    font-weight: 400;
}


/*-------------------------------------------
    06. Simple Page
---------------------------------------------*/

.simple-page > table {
    margin: 14px 0;
}

.simple-page > table > tbody > tr >td {
    border: none;
    padding: 18px 6px 18px 0;
}

.td-main {
    color: #3a3a3a;
    letter-spacing: 1px;
    font-size: 14px;
    margin: 0 12px 0 0;
    font-family: 'Roboto-Bold', sans-serif;
    text-transform: uppercase;
    border: none;
}

.td-second {
    word-break: break-word;
    letter-spacing: 1px;
    padding: 8px;
    border: none;
    font-size: 13px;
    margin: 0;
    font-style: italic;
    font-family: 'Roboto-Regular', serif;
}


/*-------------------------------------------
    08. Portfolio Page
---------------------------------------------*/

.portfolio-page > .info-content {
    width: calc(50% - 24px);
    float: left;
    position: relative;
    cursor: pointer;
    color: #fff;
    padding: 0 24px 0;
    margin: 12px;
    display: inline-block;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .14), 0 2px 2px 0 rgba(0, 0, 0, .098), 0 1px 5px 0 rgba(0, 0, 0, .084);
}

.portfolio-page > .info-content:hover img {
    opacity: 0.3;
}

.portfolio-page > .info-content >div > img {
    width: calc(100% + 48px);
    display: inline-block;
    background-color: #fff;
    margin-bottom: -1px;
    margin-left: -24px;
    -webkit-transition: all ease 0.6s;
    -moz-transition: all ease 0.6s;
    -o-transition: all ease 0.6s;
    transition: all ease 0.6s;
}

.portfolio-avatar {
    width: 45px !important;
    height: 45px !important;
    border-radius: 50%;
    z-index: 11;
    left: 12px;
    top: 12px;
    position: absolute;
    -webkit-transition: all ease 0.6s;
    -moz-transition: all ease 0.6s;
    -o-transition: all ease 0.6s;
    transition: all ease 0.6s;
    margin-top: -20px;
}

.portfolio-page > .info-content >div> .user-content {
    width: 100%;
    height: auto;
    min-height: 120px;
    position: absolute;
    bottom: 0;
}

.portfolio-page > .info-content:hover .info-title {
    margin-top: 0 !important;
}

.portfolio-page > .info-content:hover .portfolio-avatar {
    margin-top: 0 !important;
    opacity: 1;
}

.portfolio-page > .info-content:hover .cover {
    opacity: 1;
}

.cover {
    width: 100%;
    height: 100%;
    height: 0;
    position: absolute;
    top: 0;
    opacity: 0;
    left: 0;
    z-index: 2;
    -webkit-transition: all ease 0.6s;
    -moz-transition: all ease 0.6s;
    -o-transition: all ease 0.6s;
    transition: all ease 0.6s;
}

.portfolio-page > .info-content >div> .info-title:hover {
    color: #fff;
}

.portfolio-page > .info-content >div>.cover> .info-title {
    letter-spacing: 1.1px;
    margin-top: 120px;
    padding-top: 12px;
    cursor: pointer;
    color: #FFF;
    position: absolute;
    top: 38px;
    font-family: 'Roboto-Bold';
    left: 68px;
    text-transform: uppercase;
    margin-right: 64px;
    font-weight: 200;
    font-size: 1.8em;
    -webkit-transition: all ease 0.6s;
    -moz-transition: all ease 0.6s;
    -o-transition: all ease 0.6s;
    transition: all ease 0.6s;
}

.portfolio-page > .info-content >div> div > .icon {
    position: absolute;
    width: auto;
    border-bottom: 1px solid rgba(200, 200, 200, 0.3);
    height: auto;
    display: inline-block;
    padding: 6px;
    background-color: transparent;
    left: 68px;
    font-size: 12px;
    font-style: italic;
    color: #eee;
    letter-spacing: 1px;
    font-family: 'Roboto-Regular';
    top: 14px;
}


/*-------------------------------------------
    16. Top Nav
---------------------------------------------*/

.menu {
    width: 30px;
    height: 30px;
    background-image: url('/img/icons/close.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    cursor: pointer;
    position: fixed;
    right: 0;
    margin: 8px 12px;
    top: 0;
    z-index: 99;
}

.menu-cont {
    width: auto;
    min-width: 220px;
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    background-color: #eee;
    box-shadow: -1px 0px 15px #A0A0A0;
}

.top-nav {
    width: 100%;
    min-height: 220px;
    position: relative;
    display: block;
    z-index: 3;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .14), 0 2px 2px 0 rgba(0, 0, 0, .098), 0 1px 5px 0 rgba(0, 0, 0, .084);
    -webkit-transition: all ease 0.6s;
    -moz-transition: all ease 0.6s;
    -o-transition: all ease 0.6s;
    transition: all ease 0.6s;
}

.top-nav-socials {
    width: auto;
    position: relative;
    z-index: 5;
    float: right;
    height: auto;
    margin-top: 6px;
    margin-right: 25px;
    float: right;
}

ul.top-nav-socials li {
    padding: 0;
    float: left;
    margin: 0 6px;
    list-style: none;
    margin: 0;
}

.social-icons {
    background-color: transparent;
    z-index: 6;
    color: #fff;
    padding: 2px;
    list-style: none;
    margin: 8px;
    float: left;
    font-size: 14px;
    background-size: cover;
}

a.social-icons:hover,
a.social-icons:focus,
a.social-icons:active {
    color: #000;
    text-decoration: none;
}


/*-------------------------------------------
    17. Menu List
---------------------------------------------*/

.menu-list {
    padding: 0;
    margin: 0;
}

.menu-list > li {
    padding: 12px 0;
    margin: 12px 0;
    list-style: none;
}

.menu-list > li .fa {
    background-color: transparent;
    position: absolute;
    left: 28px;
    font-size: 14px;
    line-height: 20px;
    font-size: 14px;
    color: rgba(45, 45, 45, 0.9);
}

.menu-list > li > div .fa {
    background-color: transparent;
    position: absolute;
    left: 28px;
    font-size: 14px;
    line-height: 20px;
    font-size: 14px;
    color: rgba(45, 45, 45, 0.9);
}

.child-data {
    position: relative;
    display: block;
    z-index: 33;
    list-style: square !important;
    padding: 12px 2px 12px 54px;
}

.child-data li {
    padding: 8px 2px !important;
    color: #eee !important;
}

.child-data li,
.menu-list> li > span,
.menu-list > li > div >span {
    text-transform: uppercase;
    padding-left: 60px;
    margin: 0;
    border-radius: 0;
    /*color: rgba(45, 45, 45, 0.9);*/
    cursor: pointer;
    display: block;
    align-items: inherit;
    line-height: 20px;
    overflow: hidden;
    text-align: left;
    font-size: 14px;
    letter-spacing: 1.2px;
    text-decoration: none;
    white-space: normal;
    font-family: 'Roboto-Bold', sans-serif;
}

.menu-list > li:hover {
    cursor: pointer;
}

.menu-list > li:hover div > .fa,
.menu-list > li:hover .fa,
.menu-list > li:hover div > span,
.menu-list > li:hover span {
    text-decoration: none;
    cursor: pointer;
    opacity: 0.8;
}

.menu-list > li .child-data li:hover>div>span {
    text-decoration: none;
    cursor: pointer;
    opacity: 1;
}


/*-------------------------------------------
    18. Animations
---------------------------------------------*/

.toggle {
    -webkit-transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -moz-transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -ms-transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -o-transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -webkit-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -moz-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -ms-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -o-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.toggle.ng-enter {
    opacity: 0;
    transition-duration: 400ms;
    -webkit-transition-duration: 400ms;
}

.toggle.ng-enter-active {
    opacity: 1;
}

.toggle.ng-leave {
    opacity: 1;
    transition-duration: 400ms;
    -webkit-transition-duration: 400ms;
}

.toggle.ng-leave-active {
    opacity: 0;
}

.toggle.ng-hide-add {
    transition-duration: 400ms;
    -webkit-transition-duration: 400ms;
    opacity: 1;
}

.toggle.ng-hide-add.ng-hide-add-active {
    opacity: 0;
}

.toggle.ng-hide-remove {
    transition-duration: 400ms;
    -webkit-transition-duration: 400ms;
    display: block !important;
    opacity: 0;
}

.toggle.ng-hide-remove.ng-hide-remove-active {
    opacity: 1;
}

.slide-top {
    -webkit-transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -moz-transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -ms-transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -o-transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition: all 0 cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -webkit-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -moz-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -ms-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -o-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.slide-top.ng-enter {
    transform: translateY(60px);
    -ms-transform: translateY(60px);
    -webkit-transform: translateY(60px);
    transition-duration: 400ms;
    -webkit-transition-duration: 400ms;
    opacity: 0;
}

.slide-top.ng-enter-active {
    transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    opacity: 1;
}

.slide-top.ng-leave {
    transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transition-duration: 400ms;
    -webkit-transition-duration: 400ms;
    opacity: 1;
}

.slide-top.ng-leave-active {
    transform: translateY(60px);
    -ms-transform: translateY(60px);
    -webkit-transform: translateY(60px);
    opacity: 0;
}

.slide-top.ng-hide-add {
    transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transition-duration: 400ms;
    -webkit-transition-duration: 400ms;
    opacity: 1;
}

.slide-top.ng-hide-add.ng-hide-add-active {
    transform: translateY(60px);
    -ms-transform: translateY(60px);
    -webkit-transform: translateY(60px);
    opacity: 0;
}

.slide-top.ng-hide-remove {
    transform: translateY(60px);
    -ms-transform: translateY(60px);
    -webkit-transform: translateY(60px);
    transition-duration: 400ms;
    -webkit-transition-duration: 400ms;
    display: block !important;
    opacity: 0;
}

.slide-top.ng-hide-remove.ng-hide-remove-active {
    transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    opacity: 1;
}

@-webkit-keyframes rotating {
    from {
        -ms-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -ms-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -webkit-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes rotating {
    from {
        -ms-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -ms-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -webkit-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}


